<template>
  <div class="container">
    <!-- Form -->
    <form action="">
      <div class="form">
        <label for="bDate" :class="{ leErr: errorStyle }">D A Y</label>
        <input type="text" :class="{ inErr: errorStyle }" name="bDate" id="bDate" placeholder="DD"
          v-model="this.dateOfBirth.bDate" />
        <p>{{ this.errors.bDate }}</p>
      </div>
      <div class="form">
        <label for="bMonth" :class="{ leErr: errorStyle }">M O N T H</label>
        <input type="text" :class="{ inErr: errorStyle }" name="bMonth" id="bMonth" placeholder="MM"
          v-model="this.dateOfBirth.bMonth" />
        <p>{{ this.errors.bMonth }}</p>
      </div>
      <div class="form">
        <label for="bYear" :class="{ leErr: errorStyle }">Y E A R</label>
        <input type="text" :class="{ inErr: errorStyle }" name="bYear" id="bYear" placeholder="YYYY"
          v-model="this.dateOfBirth.bYear" />
        <p>{{ this.errors.bYear }}</p>
      </div>
    </form>
    <!-- Divider and Button -->
    <div id="divider">
      <hr class="divider-line" />
      <div class="btn" @click="calculateAge()">
        <img src="../assets/icon-arrow.svg" alt="btn" />
      </div>
    </div>

    <!-- Age Results -->
    <h1>
      <span style="color: var(--Purple)">{{ this.age.ageYear }}</span> years
    </h1>
    <h1>
      <span style="color: var(--Purple)">{{ this.age.ageMonth }}</span> months
    </h1>
    <h1>
      <span style="color: var(--Purple)">{{ this.age.ageDay }}</span> days
    </h1>
  </div>
  <div class="attribution">
    Challenge by <a href="https://www.frontendmentor.io?ref=challenge" target="_blank">Frontend Mentor</a>.
    Coded by <a href="#">Ayush</a>.
  </div>
</template>


<script>
export default {
  name: "AgeForm",
  data() {
    return {
      // Data for storing user input and age results
      dateOfBirth: { bDate: "", bMonth: "", bYear: "" },
      age: { ageDay: "--", ageMonth: "--", ageYear: "--" },
      // Data for storing errors
      errors: {},
      errorStyle: false
    };
  },
  methods: {
    // Method to calculate age based on input and current date
    calculateAge() {
      // Reset errors
      this.errors = {};

      // Validate input
      if (!this.validateInput()) {
        return;
      }

      // Get current date
      const currentDate = new Date();
      const currentYear = currentDate.getFullYear();
      const currentMonth = currentDate.getMonth() + 1;
      const currentDay = currentDate.getDate();

      // Parse input values
      const birthDate = parseInt(this.dateOfBirth.bDate);
      const birthMonth = parseInt(this.dateOfBirth.bMonth);
      const birthYear = parseInt(this.dateOfBirth.bYear);

      // Calculate age
      let ageYear = currentYear - birthYear;
      let ageMonth = currentMonth - birthMonth;
      let ageDay = currentDay - birthDate;

      if (ageDay < 0) {
        ageMonth -= 1;
        ageDay += this.daysInMonth(birthMonth, birthYear);
      }

      if (ageMonth < 0) {
        ageYear -= 1;
        ageMonth += 12;
      }

      // Update age results
      this.age.ageYear = ageYear;
      this.age.ageMonth = ageMonth;
      this.age.ageDay = ageDay;
    },
    // Method to validate input
    validateInput() {
      let valid = true;

      // Validate day
      const birthDate = parseInt(this.dateOfBirth.bDate);
      if (isNaN(birthDate)) {
        this.errors.bDate = "This field is required";
        valid = false;
        this.errorStyle = true;
      } else if (birthDate < 1 || birthDate > 31) {
        this.errors.bDate = "Must be a valid day";
        valid = false;
        this.errorStyle = true;
      }
      else {
        this.errors.bDate = "";
      }

      // Validate month
      const birthMonth = parseInt(this.dateOfBirth.bMonth);
      if (isNaN(birthMonth)) {
        this.errors.bMonth = "This field is required";
        valid = false;
        this.errorStyle = true;
      } else if (birthMonth < 1 || birthMonth > 12) {
        this.errors.bMonth = "Must be a valid month";
        valid = false;
        this.errorStyle = true;
      }
      else {
        this.errors.bMonth = "";
      }

      // Validate year
      const birthYear = parseInt(this.dateOfBirth.bYear);
      if (isNaN(birthYear)) {
        this.errors.bYear = "This field is required";
        valid = false;
        this.errorStyle = true;
      } else if (birthYear > new Date().getFullYear()) {
        this.errors.bYear = "Must be in the past";
        valid = false;
        this.errorStyle = true;
      }
      else {
        this.errors.bYear = "";
      }

      return valid;
    },
    // Helper function to get the number of days in a month
    daysInMonth(month, year) {
      return new Date(year, month, 0).getDate();
    }
  }
};
</script>



<style scoped>
.container {
  margin: auto;
  width: 34rem;
  height: 23rem;
  padding: 3rem;
  border-radius: 1rem 1rem 10rem 1rem;
  background-color: var(--White);
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1px;
  align-items: flex-start;
  color: var(--Smokey-grey);
}

/* error message */
.form p {
  color: var(--Light-red);
  font-size: .870em;
}

/* Divider and Button */
#divider {
  display: flex;
  align-items: center;
}

.divider-line {
  width: 90%;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0;
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  background-color: var(--Purple);
  padding: auto;
}

.btn:hover {
  background-color: var(--Off-black);
}

.btn img {
  position: absolute;
  margin: auto;
}

/* Form Input */
form {
  display: flex;
  gap: 2rem;
  align-items: flex-start;
}

label {
  font-weight: 700;
  font-size: 12px;
}

.leErr {
  color: var(--Light-red);
}

input {
  height: 4rem;
  width: 8rem;
  font-weight: 700;
  font-size: 32px;
  border-radius: 5px;
  color: var(--Off-black);
  padding: 0 0 0 1rem;
  border: 1px solid var(--Smokey-grey);
}

input:focus {
  outline: none !important;
  border: 1px solid var(--Purple);
}

.inErr {
  border: 1px solid var(--Light-red);
}

h1 {
  font-style: italic;
  font-weight: 800;
  font-size: 70px;
  text-align: left;
  line-height: 5rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

/* Responsive Styling */

@media screen and (max-width: 660px) {
  .container {
    width: 26rem;
  }

  input {
    width: 6rem;
  }

  h1 {
    font-size: 60px;
  }
}

@media screen and (max-width: 480px) {
  .container {
    width: 20rem;
    height: 28rem;
    margin: auto;
  }

  #divider {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
    /* Add margin-bottom for spacing */
  }

  hr {
    margin-top: 4rem;
    /* Add margin-top for spacing */
  }

  h1 {
    font-size: 50px;
  }

  .btn {
    margin-right: auto;
    margin-left: auto;
    margin-top: -2rem;
    /* Adjust the margin-top to prevent overlap */
  }

  input {
    width: 5rem;
    height: 3rem;
    font-size: 28px;
    padding: 6px;
  }
}
</style>